<template>
<v-card width="480" height="auto" class="mx-auto mb-2">
    <v-card-title class="text-h5 mb-2">{{item.name}}</v-card-title>

    <v-card-subtitle>{{item.value}}</v-card-subtitle>

    <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn icon @click="onEdit(item.name)" color="primary">
            <v-icon dark>mdi-pencil</v-icon>
        </v-btn>
    </v-card-actions>
</v-card>
  
</template>

<script>
export default {
  name: "ListItem",
  props: {
    item: [Object],
  },
  data() {
    return {};
  },
  methods: {
    onEdit(name) {
      this.$emit('editItem', name);
    }
  },
};
</script>


