<template>
  <div>
    <EditForm :item="currentItem" @onSave="onSave" />
    <ListItems :items="items" @editItem="onEdit" />
  </div>
</template>

<script>
import EditForm from '@/components/EditForm.vue'
import ListItems from '@/components/ListItems.vue'

export default {
    name: 'EditFormView',
    components: {
      EditForm,
      ListItems
    },
    data() {
      return {
        items: [
          {name: "ВКонтакте", value: "32"},
          {name: "Одноклассники",value: "22"},
          {name: "Facebook", value: "12"},
          {name: "Сбербанк",value: "13"},
          {name: "Тинькофф", value: "16"},
          {name: "Ozon",value: "17"},
          {name: "Мегафон", value: "18"},
          {name: "Qiwi Wallet",value: "19"},
          {name: "HeadHunter", value: "20"},
          {name: "TikTok",value: "21"},
          {name: "Likee", value: "23"},
          {name: "Wildberries",value: "24"},
          {name: "AliExpress", value: "25"},
          {name: "Юла",value: "26"},
          {name: "Uber", value: "27"},
          {name: "Яндекс.Маркет",value: "28"},
          {name: "Pinterest", value: "29"},
          {name: "Sunlight",value: "33"},
          {name: "YouTube", value: "36"},
          {name: "Discord",value: "58"},
          
        ],
        currentItem: null
      }
    },
    methods: {
      onEdit(name) {
        const index = this.items.findIndex(it => it.name == name);
        this.currentItem = this.items[index];
      },
      onSave(item) {
        const index = this.items.findIndex(it => it.name == this.currentItem.name);
        this.items.splice(index, 1, {...item});
      }
    }
  }
</script>