<template>
  <v-list>
    <v-list-item
      v-for="item in items"
      :key="item.name"
    >
      <ListItem :item="item" @editItem="onEdit" />
    </v-list-item>
  </v-list>
</template>

<script>
import ListItem from './ListItem.vue'

export default {
  name: 'ListItems',
  components: {
    ListItem
  },
  props: {
    items: [Array]
  },
  data() {
    return {
    }
  },
  methods: {
    onEdit(name) {
      this.$emit('editItem', name);
    }
  }
  
}
</script>


