<template>
  <v-content class="add-form-content">
    <v-container class="fill-height add-form-container" fluid>
      <v-row align="center" justify="center">
        <v-col cols="12" sm="8" md="4">
          <v-card elevation="12" class="add-form-card">
            <v-row align="center" justify="center">
              <v-col cols="12" md="12">
                <v-card-title class="add-form-card-title">Форма для редактирования записей</v-card-title>
                <v-card-text>
                  <v-form ref="form">
                    <v-text-field
                      label="Название"
                      name="name"
                      type="text"
                      color="blue darken-1"
                      v-model="name"
                      disabled
                    />
                    <v-text-field
                      label="Значение"
                      name="value"
                      color="blue darken-1"
                      v-model="value"
                    />
                    <div class="text-center mt-3">
                      <v-btn 
                        rounded 
                        block
                        color="blue darken-1" 
                        dark
                        @click="onSave"
                      >
                        Сохранить
                      </v-btn
                      >
                    </div>
                  </v-form>
                </v-card-text>
              </v-col>
            </v-row>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </v-content>
</template>

<script>
export default {
  name: "EditForm",
  props: {
    item: [Object]
  },
  data() {
    return {
      name: '',
      value: ''
    };
  },
  watch: {
    item(val) {
      if(Object.keys(val).length) {
        this.name = val.name;
        this.value = val.value;
      }
    }
  },
  methods: {
    onSave() {
      this.$emit('onSave', {name: this.name, value: this.value});
      this.name = "";
      this.value = "";
    }
  }
};
</script>

<style scoped>
.add-form-content {
  position: sticky;
  top: 0;
  z-index: 99;
  margin-bottom: 1rem;
}

.add-form-container {
  margin-top: -0.8rem;
}

.add-form-card {
  padding: 15px;
}

.add-form-card-title {
  display: block;
  text-align: center;
}
</style>
